import React, { Component } from 'react';
import ImageBox from './ImageBox';
import InputBox from './InputBox';

class EquipBox extends Component {
    constructor(props){
      super(props);
      this.state ={
        imageUrl: '',
        itemName: 'None',
        itemEquipped: false
      }
      this.itemNames = [];
    }

    equipItem = (e) => {
      var itemOn = true;         
      var newState = {
        itemName:  e.target.value,
        imageUrl:  e.target.value.replace(/ /g,"_") + '_icon.png',
        itemEquipped: itemOn
      };
      this.setState(newState, function(){
        var temp = this.state.itemName;
        this.props.update(this.props.type, this.props.data.find(function(e){   
            if(e.name === temp){
              return e;
            }
        }));
      });   
    }

    componentWillMount(){
      for(var i=0; i < this.props.data.length; i++){
        this.itemNames[i] = this.props.data[i].name;
      }
      this.itemNames.sort();
    }

    componentDidMount(){
      var e = {target:{value: 'Null'}};
      this.equipItem(e);
    }

    render() {
      return (
        <div className="col-lg-3 col-md-3" style={{padding:'5px'}}>
          <div className="ffxiEquipBox">
            <div className="equipmentHolder">
              <ImageBox imageUrl={this.state.imageUrl} type={this.props.type}  itemOn={this.state.itemEquipped} />
              <div className="itemName text-center"><span>{this.state.itemName}</span></div>
              <InputBox  itemData={this.itemNames} equipItem={this.equipItem}/>
            </div> 
          </div>
        </div>     
      );
    }
  }
  
  export default EquipBox;