import React, { Component } from 'react';

class StatBox extends Component {

  render() {
      return (
        <div className="col-lg-4 col-md-4 col-sm-6 col-xs-12 text-center statsPanel">
            <div><span style={{fontWeight:'bold'}}>{this.props.title}</span></div>
            <div className="row">
              {Object.keys(this.props.stats).map(function(key){
                  return <div className="col-lg-6 col-md-6 col-sm-12 col-xs-12" key={key}><span>{key}: {this.props.stats[key]}</span></div>;
              },this)}  
            </div> 
        </div>
      );
    }
  }
  
  export default StatBox;