import React, { Component } from 'react';


class ImageBox extends Component {
  constructor(props){
    super(props);
    this.type = ''
  }

  componentWillMount(){
    if(this.props.type === 'sub'){
      this.type = 'main';
    } else if(this.props.type === 'lring' || this.props.type === 'rring'){
      this.type = 'ring';
    } else if(this.props.type === 'lear' || this.props.type === 'rear'){
      this.type = 'ear';
    } else{
      this.type = this.props.type;
    }
  }

  render() {
      return (
        <div className="imageCont">
            <div className="ffxiEquipPicture" style={{fontSize:"10px"}}>
                <img className="equipmentImage" style={{display: this.props.itemOn ? 'block' : 'none'}} 
                src={'./img/' + this.type  + '/' + this.props.imageUrl} alt={'Item display'}></img>
            </div>
        </div>
      );
    }
  }
  
  export default ImageBox;