import React, { Component } from 'react';

class AddGear extends Component {
    constructor(props){
        super(props);
        this.itemString = React.createRef();
    }


    render() {
      return (
        <div className="row">
            <div className="col-lg-8 col-md-8 col-sm-12 col-xs-12 offset-lg-2 offset-md-2">
                <textarea ref={this.itemString} style={{width:'100%',height:'250px',fontSize:'12px'}}></textarea>
                <br />
                <button className="btn btn-primary" onClick={this.props.changeSet(this.refs.itemString.nodeValue)}>Equip</button>
            </div>
        </div>
      );
    }
}
  
export default AddGear;