import React, { Component } from 'react';
import './css/App.css';
import EquipBox from './EquipBox';
import AddGear from './AddGear';
import StatBox from './StatBox';
import Calculate from './Calculate';
import mainData from './json/mainList.json';
import rangeData from './json/rangeList.json';
import ammoData from './json/ammoList.json';
import headData from './json/headList.json';
import neckData from './json/neckList.json';
import earData from './json/earList.json';
import bodyData from './json/bodyList.json';
import handsData from './json/handsList.json';
import ringData from './json/ringList.json';
import backData from './json/backList.json';
import waistData from './json/waistList.json';
import legsData from './json/legsList.json';
import feetData from './json/feetList.json';

class EquipmentList extends Component {
  constructor(props){
    super(props);
    this.state = {
      baseStats:  {},
      meleeStats: {},
      magicStats: {},
      isVisible: false
    }

    this.main = undefined;  this.sub = undefined;   this.range = undefined; this.ammo = undefined;
    this.head = undefined;  this.neck = undefined;  this.lear = undefined;  this.rear = undefined;
    this.body = undefined;  this.hands = undefined; this.lring = undefined; this.rring = undefined;
    this.back = undefined;  this.waist = undefined; this.legs = undefined;  this.feet = undefined;

  }

  applyCustomSet = (value) =>{

  }

  openPanel = () => {
    this.setState(prevState => ({
      isVisible: !prevState.isVisible,
      baseStats: prevState.baseStats,
      meleeStats: prevState.meleeStats,
      magicStats: prevState.magicStats
    }));
  }

  updateItem = (type, item) => {
    this[type] = item;
    var total = this.buildNewTotal();
    this.setState(total,function(){
    });
  }

  buildNewTotal = () => {
    var temp = {};
    var cal = new Calculate();
    var equipList = [this.main, this.sub, this.range, this.ammo, 
                    this.head, this.neck, this.lear, this.rear, 
                    this.body, this.hands, this.lring, this.rring, 
                    this.back, this.waist, this.legs, this.feet];
    temp.baseStats = cal.addBaseStats(equipList, 'baseStats');
    temp.meleeStats = cal.addMeleeStats(equipList,'meleeStats');
    temp.magicStats = cal.addMagicStats(equipList, 'magicStats');
    
    return temp;
  }

  render() {
    return (
      <div className="container-fluid EquipmentListHolder">
        <div className="EquipmentList container-fluid text-center" style={{width:'60%',backgroundImage: "url('./img/ffxiEquipBg.png')"}}>
          <div className="row equal spacedRow">
            <EquipBox data={mainData} type={'main'} update={this.updateItem}/>
            <EquipBox data={mainData} type={'sub'} update={this.updateItem}/>
            <EquipBox data={rangeData} type={'range'} update={this.updateItem}/>
            <EquipBox data={ammoData} type={'ammo'} update={this.updateItem}/>
          </div>
          <div className="row equal spacedRow">
            <EquipBox data={headData} type={'head'} update={this.updateItem}/>
            <EquipBox data={neckData} type={'neck'} update={this.updateItem}/>
            <EquipBox data={earData} type={'lear'} update={this.updateItem}/>
            <EquipBox data={earData} type={'rear'} update={this.updateItem}/>
          </div>
          <div className="row equal spacedRow">
            <EquipBox data={bodyData} type={'body'} update={this.updateItem} />
            <EquipBox data={handsData} type={'hands'} update={this.updateItem} />
            <EquipBox data={ringData} type={'lring'} update={this.updateItem}/>
            <EquipBox data={ringData} type={'rring'} update={this.updateItem}/> 
          </div>
          <div className="row equal spacedRow">
            <EquipBox data={backData} type={'back'} update={this.updateItem}/>
            <EquipBox data={waistData} type={'waist'} update={this.updateItem}/>
            <EquipBox data={legsData} type={'legs'} update={this.updateItem}/>
            <EquipBox data={feetData} type={'feet'} update={this.updateItem}/>
          </div>
          {!this.state.isVisible && <button className="btn btn-primary" onClick={this.openPanel}>Add Your Gear</button>}
          {this.state.isVisible && <AddGear changeSet={this.applyCustomSet}/>}
        </div>
        <div className="row">
        <StatBox title={'Base Stats'} stats={this.state.baseStats} />
        <StatBox title={'Melee Stats'} stats={this.state.meleeStats} />
        <StatBox title={'Magic Stats'} stats={this.state.magicStats} />
      </div>
    </div>
    );
  }
}

export default EquipmentList;
